import imagenes from "/Portafolio_cliente/src/assets/imagenes";

const About = () => {
    return (
        <>
            <div id="main-container">
                <div className="hero-image">
                    <img src={imagenes.flippy2} width="50%" height="50%" />
                    <div className="hero-text">
                        <h1 className="heroh1">Acerca de mi</h1>
                    </div>
                </div>
                <center>
                    <div className="box">
                        <div id="card-container">
                            <div id="card">
                                <div className="front face">
                                    <img className="Imag" src={imagenes.logo2} />
                                </div>
                                <div className="back face">
                                    <h2>YayosGD</h2>
                                    <br></br>
                                    <p className="artist">
                                        Hago cosas en Internet, hablar de tecnologa y tengo un peculiar sentido del humor.
                                    </p>
                                </div>
                            </div>
                        </div>
                        <div id="card-container">
                            <div id="card2">
                                <div className="front face">
                                    <img className="Imag" src={imagenes.rex} />
                                </div>
                                <div className="back face">
                                    <h2>SRex</h2>
                                    <br></br>
                                    <p className="artist">
                                        El alter ego SRex solo disfruta de jugar videojuegos con sus amigos y entretenerte un rato
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                </center>
            </div>
        </>
    );
};

export default About;