import { Outlet, NavLink } from 'react-router-dom';
import imagenes from '/Portafolio_cliente/src/assets/imagenes'
import styles from './css/menu.module.css';
//import './js/menuDesp.js';
const Layout = () => {
    return (
        <>
            <div id={styles.sidemenu} className={styles.menuCollapsed}>
                <div id={styles.header}>
                    <div id={styles.menuBtn}>
                        <div className={styles.btnHam}></div>
                        <div className={styles.btnHam}></div>
                        <div className={styles.btnHam}></div>
                    </div>
                </div>
                <div id={styles.menuItems}>
                    <div className={styles.item}>
                        <a href="/inicio">
                            <div className={styles.icon}><img src={imagenes.logo} /></div>
                            <div className={styles.title}><span>Inicio</span></div>
                        </a>
                    </div>
                    <div className={styles.separator}>
                    </div>

                    <div className={styles.item}>
                        <a href="/about">
                            <div className={styles.icon}><img src={imagenes.icono1} /></div>
                            <div className={styles.title}><span>YayosGD</span></div>
                        </a>
                    </div>
                    <div className={styles.separator}>
                    </div>

                    <div className={styles.item}>
                        <a href="/trabajos">
                            <div className={styles.icon}><img src={imagenes.icono2} /></div>
                            <div className={styles.title}><span>Trabajos</span></div>
                        </a>
                    </div>
                    <div className={styles.separator}>
                    </div>

                    <div className={styles.item}>
                        <a href="/contacto">
                            <div className={styles.icon}><img src={imagenes.icono3} /></div>
                            <div className={styles.title}><span>Contacto</span></div>
                        </a>
                    </div>
                    <div className={styles.separator}></div>

                    <div className={styles.item}>
                        <a href="/tienda">
                            <div className={styles.icon}><img src={imagenes.icono4} /></div>
                            <div className={styles.title}><span>Tienda</span></div>
                        </a>
                    </div>
                    <div className={styles.separator}>
                    </div>

                    <div className={styles.item}>
                        <a href="/login">
                            <div className={styles.icon}><img src={imagenes.login} /></div>
                            <div className={styles.title}><span>Login</span></div>
                        </a>
                    </div>
                    <div className={styles.separator}>
                    </div>
                </div>
            </div>

            

            <main style={{ paddings: '20px' }}>
                <Outlet />
            </main>
        </>
    );
};

export default Layout;

//<script src="js/menuDesp.js"></script>