//import axios from 'axios';
import React, { useState } from 'react';
import { useNavigate } from 'react-router';
import UsuarioServicio from '../servicios/UsuarioServicios';
import { ROLES } from '../utils/Constantes';
import { encryptStorage } from '../utils/Storage';
import swal from 'sweetalert';
import styles from './css/registroUsuario.module.css';

const Registro = () => {
    let navigate = useNavigate();
    const [mail, setCorreo] = useState();
    const [password, setPassword] = useState();
    var hoy = new Date();

    let register = async (e) => {
        e.preventDefault();
        try {
            let res = await fetch(
                'http://localhost:9596/administrador/registrar', {
                method: "POST",
                headers: {
                    Accept: 'application/json',
                    "content-type": "application/json",

                },
                body: JSON.stringify({
                    correo: mail,
                    contrasenia: password,
                    estatus: true,
                    fechaRegistro: hoy,
                    fechaVigencia: "2022-10-15",
                    rol_id: 2
                })
            }
            );
            // alert("El usuario: " + mail + " fue registrado con xito");
            swal({
                title: "Usuario registrado con xito!",
                text: "Hola " + mail + ", ya puedes iniciar sesin!",
                icon: "success",
                button: "Aceptar"
            });
            navigate("/login");
        } catch (error) {
            console.log(error);
        }
    }

    return (
        <>
            <div className={styles.container}>
                <div className={styles.signupFrm}>
                    <div className={styles.wrapper}>
                        <form method="POST" className={styles.form} onSubmit={register}>
                            <h1 className={styles.title}>Registrarse</h1>

                            <div className={styles.inputContainer}>
                                <input type="email" className={styles.input} placeholder="a" onChange={e => setCorreo(e.target.value)} required/>
                                <label for="" className={styles.label}>Correo Electronico</label>
                            </div>

                            <div className={styles.inputContainer}>
                                <input type="password" className={styles.input} placeholder="a" onChange={e => setPassword(e.target.value)} required/>
                                <label for="" className={styles.label}>Contrasea</label>
                            </div>



                            <input type="submit" className={styles.submitBtn} value="Crear" />
                        </form>
                    </div>
                </div>
            </div>
        </>
    );
};

export default Registro;

