import imagenes from '/Portafolio_cliente/src/assets/imagenes';
import ReactPlayer from 'react-player';
import './css/inicio.css';
//import cursor from './js/cursor';
const Inicio = () => {
    return (
        <>
            <div id="main-container">
                <video autoPlay loop muted
                style={{
                    position: "absolute",
                    width:"100%",
                    height:"100%",
                    left:"50%",
                    top:"50%",
                    objectFit:"cover",
                    transform:"translate(-50%, -50%)",
                    zIndex:"-1"
                }}>
                    <source src={imagenes.fondo} type="video/mp4"/>
                </video>
                <div className="contain">
                    <canvas id="c"></canvas>
                </div>
            </div>

            <div className="social-bar">
                <a href="#" class="icon icon-facebook" target="_blank"><i class="fa fa-facebook" aria-hidden="true"></i></a>
                <a href="https://twitter.com/Yayos_TR" class="icon icon-twitter" target="_blank"><i class="fa fa-twitter" aria-hidden="true"></i></a>
                <a href="https://www.youtube.com/channel/UChzcxZ1Q4j8VYkN6JluEYuw" class="icon icon-youtube" target="_blank"><i class="fa fa-youtube-play" aria-hidden="true"></i></a>
                <a href="https://www.youtube.com/channel/UC1q_-UMEEP6pzLEP_WHJ8hw" class="icon icon-youtube2" target="_blank"><i class="fa fa-gamepad" aria-hidden="true"></i></a>
                <a href="https://www.instagram.com/this_yayos/" class="icon icon-instagram" target="_blank"><i class="fa fa-instagram" aria-hidden="true"></i></a>
                <a href="https://www.twitch.tv/yayostv" class="icon icon-twitch" target="_blank"><i class="fa fa-twitch" aria-hidden="true"></i></a>
            </div>
            <script src="js/cursor.js" type="text/javascript"></script>

        </>
    );
};

export default Inicio;

