import React, { useState } from 'react';
import { useNavigate } from 'react-router';
import UsuarioServicio from '../servicios/UsuarioServicios';
import { ROLES } from '../utils/Constantes';
import { encryptStorage } from '../utils/Storage';
//import registro from './registro';
import swal from 'sweetalert';
import styles from './css/styles.module.css';

const Login = () => {
    let navigate = useNavigate();
    const [correo, setCorreo] = useState();
    const [password, setPassword] = useState();

    let inSesion = async (e) => {
        e.preventDefault();
        try {
            let res = await fetch(
                'http://localhost:9596/administrador/login', {
                method: "POST",
                headers: {
                    Accept: 'application/json',
                    "content-type": "application/json",

                },
                body: JSON.stringify({
                    correo: correo,
                    contrasenia: password
                })
            }
            );
            if (res.status === 200) { //admin@gmail.com -> 1234
                console.log(res.status);
                navigate("/admin");
            }
            else if (res.status === 201) { //user@gmail.com -> 1234
                console.log(res.status);
                navigate("/user");
            } else {
                // alert("Usuario no identificado")
                swal({
                    title: "Error al iniciar sesin",
                    text: "El usuario y/o contrasea son incorrectos",
                    icon: "error",
                    button: "Aceptar"
                });
            }
        } catch (error) {
            console.log(error);
        }
    }

    return (
        <>
            <div className={styles.contenedor}>
                <div className={styles.form}>
                    <form method='POST' onSubmit={inSesion}>
                        <h5>Iniciar Sesin</h5>
                        <input className={styles.controls} type="text" name="usuario" placeholder='Usuario'
                            onChange={e => setCorreo(e.target.value)} />
                        <input className={styles.controls} type="password" name="contrasena" placeholder="Contrasea" required="required"
                            onChange={e => setPassword(e.target.value)} />
                        <center><input type="submit" className={styles.buttons1} value="Enviar" /></center>
                        <p><a href="/registro">Crear nuevo usuario</a></p>
                    </form>
                </div>
            </div>
        </>
    );
};

export default Login;

