import imagenes from '/Portafolio_cliente/src/assets/imagenes';
import style from './css/ContactoStyle.module.css';

const Contacto = () => {
    return (
        <>
        <div className={style.cont}>
            <div className={style.signupFrm}>
                <div className={style.wrapper}>      
                    <form action="" className={style.form} method="post">
                        <h1 className={style.title}>Contacto</h1>
                        
                        <div className={style.inputcont}>
                            <input type="text" className={style.input} required="required" placeholder="a" />
                            <label for="" className={style.label}>Nombre</label>
                        </div>
                    
                        <div className={style.inputcont}>
                            <input type="email" className={style.input} required="required" placeholder="a" />
                            <label for="" className={style.label}>Correo Electronico</label>
                        </div>
                    
                        <div className={style.inputcont}>
                            <input type="text" className={style.input} placeholder="a" />
                            <label for="" className={style.label}>Telfono</label>
                        </div>   
                    
                        <div className={style.inputcont}>
                            <input type="text" className={style.input} required="required" placeholder="a" />
                            <label for="" className={style.label}>Asunto</label>
                        </div>

                        <div className={style.inputcont2}>
                            <textarea className={style.input2} required="required" placeholder="a"></textarea>
                            <label for="" className={style.label}>Mensaje</label>
                        </div>
                        
                        <input type="submit" className={style.submitBtn} value="Enviar" />
                        
                    </form>
                </div>  
            </div>
        </div>
        </>
    );
};

export default Contacto;